html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #e8f0eb;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.right {
  margin: 0;
}
@media (min-width: 768px) {
  .right {
    display: flex;
    justify-content: right;
    width: 100%;
  }
}

h1, h2, h3 {
  font-family: "Barlow", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #34473c;
  line-height: 1.8;
}

.header {
  background-color: #141f1a;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 12rem;
  }
}
.header .logo img {
  width: 10rem;
  height: 10rem;
}
.header .logout {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 3rem 3rem;
  cursor: pointer;
  color: #f5f8f6;
  text-align: center;
  transition: transform 0.3s;
}
.header .logout svg {
  margin-bottom: 1rem;
  transition: fill 0.3s ease;
}
.header .logout:hover {
  color: #82a691;
  transform: scale(1.2);
}
.header .logout:hover svg {
  fill: #82a691;
}
@media (min-width: 768px) {
  .header .logout {
    margin: 0 0 0 4rem;
  }
}
.header .navegation {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column-reverse;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header .navegation {
    flex-direction: row;
    margin-top: 0;
  }
}
.header .menu-nav {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header .menu-nav {
    display: flex;
    flex-direction: row;
  }
}
.header .menu-nav a {
  padding: 1rem;
  font-size: 2rem;
  color: #e8f0eb;
  transition: transform 0.3s;
}
.header .menu-nav a:hover {
  color: #82a691;
  transform: scale(1.2);
}

.main {
  background-image: url(../img/background-header.webp);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #141f1a;
  width: 100%;
  max-height: 85rem;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .main {
    flex-direction: row;
    min-height: 60rem;
    padding: 5rem;
  }
  .main .information {
    padding: 0rem;
  }
}
.main .information {
  padding: 5rem;
  max-width: 100%;
}
.main .information .title {
  background: linear-gradient(to right, #adc8b8 0%, white 100%);
  color: transparent;
  background-clip: text;
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
  width: 100%;
}
.main .information .message {
  width: 100%;
  color: #e8f0eb;
  font-weight: 400;
  font-size: 2rem;
  padding: 0 1rem;
}
.main .information .message span {
  background: linear-gradient(to right, #adc8b8 0%, white 100%);
  color: transparent;
  background-clip: text;
  font-weight: 900;
}
@media (min-width: 1024px) {
  .main .information .title {
    font-size: 4rem;
  }
  .main .information .message {
    font-size: 2rem;
  }
}
.main .redirect {
  display: flex;
  justify-content: center;
  align-items: center;
}
.main .redirect .button-redirect {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 3rem;
  background-color: #141f1a;
  color: #fff;
  text-decoration: none;
  border-radius: 0.3125rem;
  transition: background-color 0.3s;
  font-size: 1.8rem;
  font-weight: 700;
  transition: transform 0.3s;
}
.main .redirect .button-redirect:hover {
  background-color: #34473c;
  transform: scale(1.2);
}
.main .redirect .button-redirect svg {
  margin-right: 1rem;
}
@media (min-width: 1024px) {
  .main .information {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 50%;
  }
  .main .information .title {
    font-size: 5rem;
  }
  .main .information .message {
    font-size: 2.5rem;
    padding: 0 3.5rem;
  }
  .main .redirect {
    width: 50%;
  }
  .main .redirect .button-redirect svg {
    max-width: 4rem;
  }
}

.contenedor {
  text-align: center;
}
.contenedor h2 {
  font-size: 4rem;
  font-weight: 700;
  color: #141f1a;
  margin: 3rem 0;
}

.botton {
  background-color: #3d5848;
  color: #e8f0eb;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin: 3rem 0 4rem 0;
  display: block;
  border: none;
  border-radius: 0.2rem;
  transition: transform 0.3s;
  margin-top: 0;
  margin-bottom: 2rem;
}
.botton:hover {
  background-color: #5c856d;
  cursor: pointer;
}
.botton:hover {
  margin-right: 1.2rem;
  transform: scale(1.2);
}
@media (min-width: 768px) {
  .botton {
    margin: 1.2rem 0;
  }
}

.container-adds {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .container-adds {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.add {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 3rem 1rem;
  border: 0.0625rem solid #ccc;
  border-radius: 0.5rem;
  background: linear-gradient(145deg, #5f8870, #3d5848);
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .add {
    margin: 0;
  }
}
.add__img {
  border-radius: 0.5rem;
  margin-bottom: 0.625rem;
}
.add__img img {
  width: 18rem;
  height: 20rem;
}
.add__name {
  font-size: 2.5rem;
  font-weight: 900;
  color: #e8f0eb;
  margin: 1rem 0;
}
.add__description {
  font-size: 1.8rem;
  margin: 1rem 0;
  color: #c2d6ca;
  margin-bottom: 2rem;
  max-height: 25rem;
  padding: 0 2rem;
}
.add__button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 3rem;
  background-color: #141f1a;
  text-decoration: none;
  border-radius: 0.3125rem;
  transition: background-color 0.3s;
  cursor: pointer;
  transition: transform 0.3s;
}
.add__button:hover {
  background-color: #34473c;
  transform: scale(1.2);
}
.add__button p {
  color: #e8f0eb;
  padding: 0;
  margin: 0;
  font-size: 1.5rem;
}
.add__button svg {
  max-width: 3rem;
  padding: 0;
  margin-right: 1rem;
}

@media (min-width: 768px) {
  .benefits-section {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.benefits-section h1 {
  color: #141f1a;
  text-align: center;
  font-size: 4rem;
  font-weight: 700;
  grid-column: 1/3;
}
.benefits-section .benefit {
  padding: 2rem;
  text-align: center;
  background-color: #f5f8f6;
  border-radius: 0.8rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
  transition: transform 0.3s ease;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.benefits-section .benefit .benefit__img img {
  width: 10rem;
  height: auto;
}
.benefits-section .benefit .benefit__title {
  font-size: 2rem;
  margin-top: 1rem;
  margin-bottom: 0.625rem;
}
.benefits-section .benefit .benefit__description {
  font-size: 1.8rem;
  color: #666;
}
.benefits-section .benefit:hover {
  transform: scale(1.05);
}

.contact {
  background-image: url(../img/contact.webp);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  max-height: 70rem;
  text-align: center;
  color: #fff;
  padding: 4rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .contact {
    height: 46rem;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contact .contact__message {
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact .contact__message h3 {
  font-weight: 700;
  font-size: 2.5rem;
}
.contact .contact__message p {
  font-size: 1.8rem;
  color: #e8f0eb;
}
@media (min-width: 768px) {
  .contact .contact__message h3 {
    font-size: 3rem;
  }
  .contact .contact__message p {
    font-size: 2rem;
  }
}
.contact .contact__icons-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact .contact__actions {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}
.contact .contact__actions a {
  display: block;
  transition: transform 0.3s;
}
.contact .contact__actions a:hover {
  transform: scale(1.2);
}
.contact .contact__actions .contact__icon img {
  width: 5rem;
  height: auto;
  margin: 0 1rem;
}
.contact .contact__button {
  margin: 5rem;
}
.contact .contact__button a {
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact .contact__button a svg {
  margin-right: 1rem;
  max-width: 5rem;
}
.contact .button {
  padding: 1.5rem 3rem;
  background-color: #141f1a;
  color: #fff;
  text-decoration: none;
  border-radius: 0.3125rem;
  transition: background-color 0.3s;
  font-size: 1.8rem;
  font-weight: 700;
}
.contact .button:hover {
  background-color: #34473c;
}

footer {
  background-color: #141f1a;
  color: #fff;
  padding-top: 4rem;
  padding-bottom: 0;
  margin-top: 4rem;
}

.footer-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}
@media (min-width: 768px) {
  .footer-container {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.footer-container .right {
  gap: 5rem;
}

.footer-logo img {
  width: 8rem;
  height: auto;
}
.footer-logo p {
  color: #e8f0eb;
  font-size: 1rem;
}

.footer-links,
.footer-social,
.footer-attributions {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.footer-links h3,
.footer-social h3,
.footer-attributions h3 {
  color: #adc8b8;
  font-size: 1.5rem;
  font-weight: 400;
  margin-bottom: 2rem;
}
.footer-links a,
.footer-social a,
.footer-attributions a {
  color: #e8f0eb;
  margin-bottom: 1rem;
  text-align: center;
}
.footer-links a:hover,
.footer-social a:hover,
.footer-attributions a:hover {
  color: #82a691;
}
@media (min-width: 768px) {
  .footer-links a,
.footer-social a,
.footer-attributions a {
    color: #e8f0eb;
    margin-bottom: 1rem;
    text-align: left;
  }
}

.footer-attributions p {
  color: #e8f0eb;
  margin-bottom: 0.5rem;
  margin-top: 0;
  text-align: center;
  font-size: 1rem;
}
.footer-attributions h3 {
  text-align: center;
}
.footer-attributions a {
  color: #adc8b8;
}
@media (min-width: 768px) {
  .footer-attributions p {
    text-align: left;
  }
  .footer-attributions h3 {
    text-align: left;
  }
}

.footer-derechos {
  height: 100%;
  background-color: #18251f;
  text-align: center;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  padding: 0 4rem;
}
.footer-derechos p {
  color: #f5f8f6;
  padding: 3rem 0;
  text-align: center;
  margin: 0;
  height: 100%;
}

.page-contact__title {
  margin: 8rem 0;
  font-size: 4rem;
  font-weight: 700;
  color: #2c3b33;
}

.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  max-height: 300rem;
  background: linear-gradient(to bottom, #2c3b33 50%, #e8f0eb 50%);
  border-radius: 5px;
}
@media (min-width: 768px) {
  .form-container {
    flex-direction: row;
    background: linear-gradient(to right, #2c3b33 50%, #e8f0eb 50%);
  }
}

.contact-socials {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 50%;
  width: 100%;
  margin: 0;
  padding: 0 2rem 2rem 2rem;
  position: relative;
  border-radius: 0 0 0 10px;
}
@media (min-width: 768px) {
  .contact-socials {
    width: 50%;
    height: 100%;
    padding: 0 10rem 10rem 10rem;
  }
}
.contact-socials .contact-socials__title {
  color: #f5f8f6;
  font-size: 3rem;
  font-weight: 700;
}
.contact-socials .contact-socials__message {
  color: #c2d6ca;
  font-size: 1.8rem;
  font-weight: 400;
  text-align: center;
}
@media (min-width: 768px) {
  .contact-socials .contact-socials__message {
    text-align: left;
  }
}
.contact-socials .contact-socials__item {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s;
  margin-left: 3rem;
}
@media (min-width: 768px) {
  .contact-socials .contact-socials__item {
    margin-left: 0;
  }
}
.contact-socials .contact-socials__item .contact-socials__label {
  color: #c2d6ca;
  font-size: 1.5rem;
  text-align: center;
  margin-left: 2rem;
}
.contact-socials .contact-socials__item:hover {
  transform: scale(1.2);
}
.contact-socials .contact-socials__item:hover .contact-socials__label {
  color: #f5f8f6;
}
.contact-socials .contact-socials__img img {
  width: 4rem;
  height: auto;
}

.form {
  padding: 2rem;
  height: 50%;
  width: 100%;
  height: 50%;
  background-color: #e8f0eb;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .form {
    width: 50%;
    height: 100%;
  }
}
.form p {
  font-size: 1.8rem;
  color: #34473c;
  margin-top: 0;
  text-align: center;
}
.form legend {
  font-size: 2rem;
  color: #2c3b33;
  padding-bottom: 1rem;
  margin-bottom: 1.5rem;
  display: block;
  width: 100%;
  font-weight: 700;
}
.form fieldset {
  border: 1px solid #c2d6ca;
  border-radius: 8px;
  margin: 1.5rem 0;
  padding: 1.5rem;
}
.form label {
  font-weight: 400;
  display: block;
  text-align: start;
  padding: 1rem 0;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #f5f8f6;
  border: 1px solid #c2d6ca;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  transition: border-color 0.3s ease;
}
.form input:not([type=submit]):focus,
.form textarea:focus,
.form select:focus {
  border-color: #5f8870;
}
.form textarea {
  height: 10rem;
  resize: none;
}
.form .type-contact {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.form .type-contact input[type=radio] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 2rem;
  height: 2rem;
  border: 2px solid #2c3b33;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  margin: 0;
}
.form .type-contact input[type=radio]:checked {
  background-color: #3d5848;
}
.form .type-contact input[type=radio]:checked + label {
  color: #34473c;
}
.form .button-form {
  background-color: #34473c;
  color: #fff;
  padding: 1rem 3rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.8rem;
  font-weight: 700;
  margin-top: 2rem;
  float: right;
  transition: background-color 0.3s ease;
  transition: transform 0.3s;
}
.form .button-form:hover {
  background-color: #547361;
  transform: scale(1.2);
}